import React, { useEffect, useState } from "react";
import { Box, Card, CardContent, FormLabel } from "@material-ui/core";
import { Field, ErrorMessage } from "formik";
import { TextField } from "formik-material-ui";
import * as yup from "yup";
import styled from "styled-components";
import { jsPDF } from "jspdf";
import "jspdf-autotable";

import Header from "../header/Header";
import FormStepper from "./FormStepper";
import FormStep from "./FormStep";
import RadioOptions from "./RadioOptions";

import FeedbackItems from "./FeedbackItems";

const Container = styled("div")`
	padding: ${(props) => (props.vertical ? 0 : "50px")};
	width: 100%;
	height: fit-content;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	font-size: 1.8rem;
	color: ${(props) => props.theme.colors.black};

	.card {
		width: ${(props) => (props.vertical ? "100%" : "90%")};
		margin-top: ${(props) => (props.vertical ? "8vh" : 0)};
		box-shadow: 0 0 60px 0 rgba(0, 0, 0, 0.1);
	}

	.box-step2 {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	.label {
		margin: 12px;
		text-align: justify;
		max-width: 80em;
	}

	.options {
		display: flex;
		flex-direction: ${(props) => (props.vertical ? "column" : "row")};
	}

	.error-msg {
		font-size: 0.8rem;
		color: red;
	}

	.step-vertical {
		text-align: center;
		padding: 24px;
	}
`;

const sleep = (time) => new Promise((acc) => setTimeout(acc, time));

function FeedbackContent(props) {
	const [vertical, setVertical] = useState(
		document.body.clientWidth <= 600 ? true : false
	);

	function todaysDate() {
		const today = new Date();
		const day = `${
			today.getDate() < 10 ? `0${today.getDate()}` : today.getDate()
		}`;
		const month = `${
			today.getMonth() + 1 < 10 ? `0${today.getMonth() + 1}` : today.getMonth()
		}`;
		const year = today.getFullYear();

		return `${year}-${month}-${day}`;
	}

	useEffect(() => {
		window.addEventListener("resize", onResize);
	}, []);

	const onResize = (e) => {
		if (document.body.clientWidth <= 600) {
			setVertical(true);
		} else {
			setVertical(false);
		}
	};

	function generatePDFTable(values) {
		const doc = new jsPDF();

		const base64Img =
			"data:image/png;base64,";

		const tableHead = [["Pilar", "Pergunta", "Avaliação", "Comentários"]];

		const dateStringArray = values.date.split("-");
		const date = `${dateStringArray[2]}/${dateStringArray[1]}/${dateStringArray[0]}`;

		const styles = { valign: "middle", halign: "center" };

		const desempenho = [
			[
				{
					content: `Desempenho`,
					rowSpan: 2,
					styles: styles,
				},
				`${FeedbackItems["desempenho"][0]}`,
				`${values.efficiency}`,
				{
					content: `${values.performanceCommentary}`,
					rowSpan: 2,
					styles: styles,
				},
			],
			[`${FeedbackItems["desempenho"][1]}`, `${values.eficacia}`],
		];

		const comportamento = [
			[
				{
					content: "Comportamento",
					rowSpan: 4,
					styles: styles,
				},
				`${FeedbackItems["comportamento"][0]}`,
				`${values.behavior}`,
				{
					content: `${values.behaviorCommentary}`,
					rowSpan: 4,
					styles: styles,
				},
			],
			[`${FeedbackItems["comportamento"][1]}`, `${values.resilience}`],
			[`${FeedbackItems["comportamento"][2]}`, `${values.challenges}`],
			[`${FeedbackItems["comportamento"][3]}`, `${values.ethic}`],
		];

		const softSkills = [
			[
				{
					content: "Habilidades Sociais e Pessoais",
					rowSpan: 7,
					styles: styles,
				},
				`${FeedbackItems["softSkills"][0]}`,
				`${values.problemResolution}`,
				{
					content: `${values.softSkillsCommentary}`,
					rowSpan: 7,
					styles: styles,
				},
			],
			[`${FeedbackItems["softSkills"][1]}`, `${values.communication}`],
			[`${FeedbackItems["softSkills"][2]}`, `${values.proactivity}`],
			[
				`${FeedbackItems["softSkills"][3]}`,
				`${values.interpersonalRelationship}`,
			],
			[`${FeedbackItems["softSkills"][4]}`, `${values.teamWork}`],
			[`${FeedbackItems["softSkills"][5]}`, `${values.teamRelationship}`],
			[`${FeedbackItems["softSkills"][6]}`, `${values.culturalFit}`],
		];

		doc.autoTable({
			head: tableHead,
			body: [...desempenho, ...comportamento, ...softSkills],
			didDrawPage: function (data) {
				// Header
				doc.setFontSize(20);
				doc.setTextColor(40);
				if (base64Img) {
					doc.addImage(
						base64Img,
						"JPEG",
						data.settings.margin.left,
						15,
						10,
						10
					);
				}
				doc.text("Feedback", data.settings.margin.left + 15, 22);

				doc.setFontSize(12);

				doc.text(
					`Relatório gerado em: ${date}\nAvaliador: ${values.myName}\nAvaliado: ${values.employeeName}`,
					data.settings.margin.left,
					32
				);
			},
			margin: { top: 50 },
			theme: "grid",
		});

		doc.save("feedback.pdf");
	}

	return (
		<>
			<Header />
			<Container vertical={vertical}>
				<Card className="card">
					<CardContent>
						<FormStepper
							vertical={vertical}
							initialValues={{
								myName: "Usuário Mock",
								employeeName: "",
								date: todaysDate(),
								efficiency: "",
								eficacia: "",
								performanceCommentary: "",
								behavior: "",
								resilience: "",
								challenges: "",
								ethic: "",
								behaviorCommentary: "",
								problemResolution: "",
								communication: "",
								proactivity: "",
								interpersonalRelationship: "",
								teamWork: "",
								teamRelationship: "",
								culturalFit: "",
								softSkillsCommentary: "",
							}}
							onSubmit={async (values) => {
								await sleep(1500);
								generatePDFTable(values);
								await sleep(1000);
							}}
						>
							<FormStep
								label="Informações Gerais"
								validationSchema={yup.object({
									myName: yup
										.string()
										.required("É necessário informar o nome do avaliador."),
									employeeName: yup
										.string()
										.required(
											"É necessário informar o nome de quem está sendo avaliado."
										),
									date: yup.date().required("É necessário informar uma data."),
								})}
							>
								<Box paddingBottom={2}>
									<Field
										fullWidth
										name="myName"
										component={TextField}
										label="Nome do avaliador"
										InputLabelProps={{
											required: true,
										}}
									/>
								</Box>
								<Box paddingBottom={2}>
									<Field
										fullWidth
										name="employeeName"
										component={TextField}
										label="Nome do avaliado"
										InputLabelProps={{
											required: true,
										}}
									/>
								</Box>
								<Box paddingBottom={2}>
									<Field
										name="date"
										type="date"
										component={TextField}
										label="Data"
										InputLabelProps={{
											shrink: true,
											required: true,
										}}
									/>
								</Box>
							</FormStep>
							<FormStep
								label="Pilar de Desempenho"
								validationSchema={yup.object().shape({
									efficiency: yup
										.string()
										.required("É necessário escolher uma das opções."),
									eficacia: yup
										.string()
										.required("É necessário escolher uma das opções."),
								})}
							>
								<Box paddingBottom={2} marginTop={4} className="box-step2">
									<div className="error-msg">
										<ErrorMessage name="efficiency" />
									</div>
									<FormLabel component="legend" className="label">
										{`${FeedbackItems["desempenho"][0]}`}
									</FormLabel>
									<div className="options">
										<RadioOptions
											name="efficiency"
											type="radio"
											position={vertical}
											value="Não Aderente"
											label="Não Aderente"
										/>
										<RadioOptions
											name="efficiency"
											type="radio"
											position={vertical}
											value="Parcialmente Aderente"
											label="Parcialmente Aderente"
										/>
										<RadioOptions
											name="efficiency"
											type="radio"
											position={vertical}
											value="Aderente"
											label="Aderente"
										/>
										<RadioOptions
											name="efficiency"
											type="radio"
											position={vertical}
											value="Modelo/Referência"
											label="Modelo/Referência"
										/>
									</div>
								</Box>
								<Box paddingBottom={2} marginTop={4} className="box-step2">
									<div className="error-msg">
										<ErrorMessage name="eficacia" />
									</div>
									<FormLabel component="legend" className="label">
										{`${FeedbackItems["desempenho"][1]}`}
									</FormLabel>
									<div className="options">
										<RadioOptions
											name="eficacia"
											type="radio"
											position={vertical}
											value="Não Aderente"
											label="Não Aderente"
										/>
										<RadioOptions
											name="eficacia"
											type="radio"
											position={vertical}
											value="Parcialmente Aderente"
											label="Parcialmente Aderente"
										/>
										<RadioOptions
											name="eficacia"
											type="radio"
											position={vertical}
											value="Aderente"
											label="Aderente"
										/>
										<RadioOptions
											name="eficacia"
											type="radio"
											position={vertical}
											value="Modelo/Referência"
											label="Modelo/Referência"
										/>
									</div>
								</Box>
								<Box paddingBottom={2} marginTop={4}>
									<Field
										fullWidth
										multiline
										variant="outlined"
										rows={5}
										name="performanceCommentary"
										component={TextField}
										label="Comentários"
									/>
								</Box>
							</FormStep>
							<FormStep
								label="Pilar de Comportamento"
								validationSchema={yup.object().shape({
									behavior: yup
										.string()
										.required("É necessário escolher uma das opções."),
									resilience: yup
										.string()
										.required("É necessário escolher uma das opções."),
									challenges: yup
										.string()
										.required("É necessário escolher uma das opções."),
									ethic: yup
										.string()
										.required("É necessário escolher uma das opções."),
								})}
							>
								<Box paddingBottom={2} marginTop={4} className="box-step2">
									<div className="error-msg">
										<ErrorMessage name="behavior" />
									</div>
									<FormLabel component="legend" className="label">
										{`${FeedbackItems["comportamento"][0]}`}
									</FormLabel>
									<div className="options">
										<RadioOptions
											name="behavior"
											type="radio"
											position={vertical}
											value="Não Aderente"
											label="Não Aderente"
										/>
										<RadioOptions
											name="behavior"
											type="radio"
											position={vertical}
											value="Parcialmente Aderente"
											label="Parcialmente Aderente"
										/>
										<RadioOptions
											name="behavior"
											type="radio"
											position={vertical}
											value="Aderente"
											label="Aderente"
										/>
										<RadioOptions
											name="behavior"
											type="radio"
											position={vertical}
											value="Modelo/Referência"
											label="Modelo/Referência"
										/>
									</div>
								</Box>
								<Box paddingBottom={2} marginTop={4} className="box-step2">
									<div className="error-msg">
										<ErrorMessage name="resilience" />
									</div>
									<FormLabel component="legend" className="label">
										{`${FeedbackItems["comportamento"][1]}`}
									</FormLabel>
									<div className="options">
										<RadioOptions
											name="resilience"
											type="radio"
											position={vertical}
											value="Não Aderente"
											label="Não Aderente"
										/>
										<RadioOptions
											name="resilience"
											type="radio"
											position={vertical}
											value="Parcialmente Aderente"
											label="Parcialmente Aderente"
										/>
										<RadioOptions
											name="resilience"
											type="radio"
											position={vertical}
											value="Aderente"
											label="Aderente"
										/>
										<RadioOptions
											name="resilience"
											type="radio"
											position={vertical}
											value="Modelo/Referência"
											label="Modelo/Referência"
										/>
									</div>
								</Box>
								<Box paddingBottom={2} marginTop={4} className="box-step2">
									<div className="error-msg">
										<ErrorMessage name="challenges" />
									</div>
									<FormLabel component="legend" className="label">
										{`${FeedbackItems["comportamento"][2]}`}
									</FormLabel>
									<div className="options">
										<RadioOptions
											name="challenges"
											type="radio"
											position={vertical}
											value="Não Aderente"
											label="Não Aderente"
										/>
										<RadioOptions
											name="challenges"
											type="radio"
											position={vertical}
											value="Parcialmente Aderente"
											label="Parcialmente Aderente"
										/>
										<RadioOptions
											name="challenges"
											type="radio"
											position={vertical}
											value="Aderente"
											label="Aderente"
										/>
										<RadioOptions
											name="challenges"
											type="radio"
											position={vertical}
											value="Modelo/Referência"
											label="Modelo/Referência"
										/>
									</div>
								</Box>
								<Box paddingBottom={2} marginTop={4} className="box-step2">
									<div className="error-msg">
										<ErrorMessage name="ethic" />
									</div>
									<FormLabel component="legend" className="label">
										{`${FeedbackItems["comportamento"][3]}`}
									</FormLabel>
									<div className="options">
										<RadioOptions
											name="ethic"
											type="radio"
											position={vertical}
											value="Não Aderente"
											label="Não Aderente"
										/>
										<RadioOptions
											name="ethic"
											type="radio"
											position={vertical}
											value="Parcialmente Aderente"
											label="Parcialmente Aderente"
										/>
										<RadioOptions
											name="ethic"
											type="radio"
											position={vertical}
											value="Aderente"
											label="Aderente"
										/>
										<RadioOptions
											name="ethic"
											type="radio"
											position={vertical}
											value="Modelo/Referência"
											label="Modelo/Referência"
										/>
									</div>
								</Box>
								<Box paddingBottom={2} marginTop={4}>
									<Field
										fullWidth
										multiline
										variant="outlined"
										rows={5}
										name="behaviorCommentary"
										component={TextField}
										label="Comentários"
									/>
								</Box>
							</FormStep>
							<FormStep
								label="Pilar de Soft Skills"
								validationSchema={yup.object().shape({
									problemResolution: yup
										.string()
										.required("É necessário escolher uma das opções."),
									communication: yup
										.string()
										.required("É necessário escolher uma das opções."),
									proactivity: yup
										.string()
										.required("É necessário escolher uma das opções."),
									interpersonalRelationship: yup
										.string()
										.required("É necessário escolher uma das opções."),
									teamWork: yup
										.string()
										.required("É necessário escolher uma das opções."),
									teamRelationship: yup
										.string()
										.required("É necessário escolher uma das opções."),
									culturalFit: yup
										.string()
										.required("É necessário escolher uma das opções."),
								})}
							>
								<Box paddingBottom={2} marginTop={4} className="box-step2">
									<div className="error-msg">
										<ErrorMessage name="problemResolution" />
									</div>
									<FormLabel component="legend" className="label">
										{`${FeedbackItems["softSkills"][0]}`}
									</FormLabel>
									<div className="options">
										<RadioOptions
											name="problemResolution"
											type="radio"
											position={vertical}
											value="Não Aderente"
											label="Não Aderente"
										/>
										<RadioOptions
											name="problemResolution"
											type="radio"
											position={vertical}
											value="Parcialmente Aderente"
											label="Parcialmente Aderente"
										/>
										<RadioOptions
											name="problemResolution"
											type="radio"
											position={vertical}
											value="Aderente"
											label="Aderente"
										/>
										<RadioOptions
											name="problemResolution"
											type="radio"
											position={vertical}
											value="Modelo/Referência"
											label="Modelo/Referência"
										/>
									</div>
								</Box>
								<Box paddingBottom={2} marginTop={4} className="box-step2">
									<div className="error-msg">
										<ErrorMessage name="communication" />
									</div>
									<FormLabel component="legend" className="label">
										{`${FeedbackItems["softSkills"][1]}`}
									</FormLabel>
									<div className="options">
										<RadioOptions
											name="communication"
											type="radio"
											position={vertical}
											value="Não Aderente"
											label="Não Aderente"
										/>
										<RadioOptions
											name="communication"
											type="radio"
											position={vertical}
											value="Parcialmente Aderente"
											label="Parcialmente Aderente"
										/>
										<RadioOptions
											name="communication"
											type="radio"
											position={vertical}
											value="Aderente"
											label="Aderente"
										/>
										<RadioOptions
											name="communication"
											type="radio"
											position={vertical}
											value="Modelo/Referência"
											label="Modelo/Referência"
										/>
									</div>
								</Box>
								<Box paddingBottom={2} marginTop={4} className="box-step2">
									<div className="error-msg">
										<ErrorMessage name="proactivity" />
									</div>
									<FormLabel component="legend" className="label">
										{`${FeedbackItems["softSkills"][2]}`}
									</FormLabel>
									<div className="options">
										<RadioOptions
											name="proactivity"
											type="radio"
											position={vertical}
											value="Não Aderente"
											label="Não Aderente"
										/>
										<RadioOptions
											name="proactivity"
											type="radio"
											position={vertical}
											value="Parcialmente Aderente"
											label="Parcialmente Aderente"
										/>
										<RadioOptions
											name="proactivity"
											type="radio"
											position={vertical}
											value="Aderente"
											label="Aderente"
										/>
										<RadioOptions
											name="proactivity"
											type="radio"
											position={vertical}
											value="Modelo/Referência"
											label="Modelo/Referência"
										/>
									</div>
								</Box>
								<Box paddingBottom={2} marginTop={4} className="box-step2">
									<div className="error-msg">
										<ErrorMessage name="interpersonalRelationship" />
									</div>
									<FormLabel component="legend" className="label">
										{`${FeedbackItems["softSkills"][3]}`}
									</FormLabel>
									<div className="options">
										<RadioOptions
											name="interpersonalRelationship"
											type="radio"
											position={vertical}
											value="Não Aderente"
											label="Não Aderente"
										/>
										<RadioOptions
											name="interpersonalRelationship"
											type="radio"
											position={vertical}
											value="Parcialmente Aderente"
											label="Parcialmente Aderente"
										/>
										<RadioOptions
											name="interpersonalRelationship"
											type="radio"
											position={vertical}
											value="Aderente"
											label="Aderente"
										/>
										<RadioOptions
											name="interpersonalRelationship"
											type="radio"
											position={vertical}
											value="Modelo/Referência"
											label="Modelo/Referência"
										/>
									</div>
								</Box>
								<Box paddingBottom={2} marginTop={4} className="box-step2">
									<div className="error-msg">
										<ErrorMessage name="teamWork" />
									</div>
									<FormLabel component="legend" className="label">
										{`${FeedbackItems["softSkills"][4]}`}
									</FormLabel>
									<div className="options">
										<RadioOptions
											name="teamWork"
											type="radio"
											position={vertical}
											value="Não Aderente"
											label="Não Aderente"
										/>
										<RadioOptions
											name="teamWork"
											type="radio"
											position={vertical}
											value="Parcialmente Aderente"
											label="Parcialmente Aderente"
										/>
										<RadioOptions
											name="teamWork"
											type="radio"
											position={vertical}
											value="Aderente"
											label="Aderente"
										/>
										<RadioOptions
											name="teamWork"
											type="radio"
											position={vertical}
											value="Modelo/Referência"
											label="Modelo/Referência"
										/>
									</div>
								</Box>
								<Box paddingBottom={2} marginTop={4} className="box-step2">
									<div className="error-msg">
										<ErrorMessage name="teamRelationship" />
									</div>
									<FormLabel component="legend" className="label">
										{`${FeedbackItems["softSkills"][5]}`}
									</FormLabel>
									<div className="options">
										<RadioOptions
											name="teamRelationship"
											type="radio"
											position={vertical}
											value="Não Aderente"
											label="Não Aderente"
										/>
										<RadioOptions
											name="teamRelationship"
											type="radio"
											position={vertical}
											value="Parcialmente Aderente"
											label="Parcialmente Aderente"
										/>
										<RadioOptions
											name="teamRelationship"
											type="radio"
											position={vertical}
											value="Aderente"
											label="Aderente"
										/>
										<RadioOptions
											name="teamRelationship"
											type="radio"
											position={vertical}
											value="Modelo/Referência"
											label="Modelo/Referência"
										/>
									</div>
								</Box>
								<Box paddingBottom={2} marginTop={4} className="box-step2">
									<div className="error-msg">
										<ErrorMessage name="culturalFit" />
									</div>
									<FormLabel component="legend" className="label">
										{`${FeedbackItems["softSkills"][6]}`}
									</FormLabel>
									<div className="options">
										<RadioOptions
											name="culturalFit"
											type="radio"
											position={vertical}
											value="Não Aderente"
											label="Não Aderente"
										/>
										<RadioOptions
											name="culturalFit"
											type="radio"
											position={vertical}
											value="Parcialmente Aderente"
											label="Parcialmente Aderente"
										/>
										<RadioOptions
											name="culturalFit"
											type="radio"
											position={vertical}
											value="Aderente"
											label="Aderente"
										/>
										<RadioOptions
											name="culturalFit"
											type="radio"
											position={vertical}
											value="Modelo/Referência"
											label="Modelo/Referência"
										/>
									</div>
								</Box>
								<Box paddingBottom={2} marginTop={4}>
									<Field
										fullWidth
										multiline
										variant="outlined"
										rows={5}
										name="softSkillsCommentary"
										component={TextField}
										label="Comentários"
									/>
								</Box>
							</FormStep>
						</FormStepper>
					</CardContent>
				</Card>
			</Container>
		</>
	);
}

export default FeedbackContent;
